import React, { useContext, useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { store } from "store/store";
import "./Articles__List.css";

function Articles__List() {
  // history 사용
  const history = useHistory();
  // 전역 상태 관리 (store)
  const globalState = useContext(store);
  const { value } = globalState;
  const { articles } = value;

  const [showAll, setShowAll] = useState(true);
  const [articleList, setArticleList] = useState([]);

  useEffect(() => {
    if (showAll) {
      setArticleList(
        articles.reduce((acc, curr) => {
          acc.push(...curr.itemList);
          return acc;
        }, [])
      );
    }
  }, [showAll]);

  // 글 선택
  const selectArticle = event => {
    const path = event.target.getAttribute("value");
    history.push(`/articles/${path}`);
  };

  return (
    <div className="Articles__List">
      {articleList.map((article, idx) => (
        <div
          key={idx}
          className="Articles__List--Article"
          onClick={selectArticle}
          value={`${article.category}/${article.id}`}
        >
          <div
            className="Article__Title"
            value={`${article.category}/${article.id}`}
          >
            <h1 value={`${article.category}/${article.id}`}>{article.title}</h1>
          </div>
          <div
            className="Article__Category-Date"
            value={`${article.category}/${article.id}`}
          >
            <div
              className="Article__Category"
              value={`${article.category}/${article.id}`}
            >
              {article.category}
            </div>
            <div className="Article__Date">
              value={`${article.category}/${article.id}`}
              {`
                ${article.date.split(" ")[2]}
                ${article.date.split(" ")[1]}
                ${article.date.split(" ")[3]}
              `}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

export default Articles__List;
